body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

textarea {
  display: block;
  width: 100%;
  height: 1.1em;
  font-size: 1.5em;
}
#header {
  font-size: 2em;
  padding: 0.5em;
  width: 100%;
  background-color: darkgrey;
  text-align: center;
}

#sub-header {
  font-size: 0.75em;
  padding: 0.5em;
  width: 100%;
  text-align: center;
  background-color: grey;
  margin-bottom: 1em;
}

#button-container,
#modifierContainer,
#textfieldContainer {
  width: 100%;
  margin: 0.5em;
  padding: 0.5em;
  text-align: center;
}

#modifierContainer {
  display: flex;
  flex-wrap: wrap;
}

.modifier {
  display: inline-block;
  width: 50%;
  padding: 1em;
  text-align: left;
}

.modifier .circle-picker,
.modifier .rc-slider {
  padding-top: 1em;
}
canvas { display: none; }

img { margin: 0 auto; }
