#header {
  font-size: 2em;
  padding: 0.5em;
  width: 100%;
  background-color: darkgrey;
  text-align: center;
}

#sub-header {
  font-size: 0.75em;
  padding: 0.5em;
  width: 100%;
  text-align: center;
  background-color: grey;
  margin-bottom: 1em;
}

#button-container,
#modifierContainer,
#textfieldContainer {
  width: 100%;
  margin: 0.5em;
  padding: 0.5em;
  text-align: center;
}

#modifierContainer {
  display: flex;
  flex-wrap: wrap;
}

.modifier {
  display: inline-block;
  width: 50%;
  padding: 1em;
  text-align: left;
}

.modifier .circle-picker,
.modifier .rc-slider {
  padding-top: 1em;
}